// components/FiltersPanel.js
import React from 'react';

export default function FiltersPanel({
  level,
  status,
  onSetLevel,
  onSetStatus,
  minPrice,
  maxPrice,
  onSetMinPrice,
  onSetMaxPrice,
  sort,
  onSetSort,
  onApply
}) {
  const levelOptions = ['All','Collector','Premium','Cute'];
  const statusOptions = ['All','In Stock','Pre-order'];

  return (
    <aside className="panel" aria-label="Filters">
      <h4>Filters</h4>

      <div style={{marginTop:8}}>
        <h4>Product Level</h4>
        <div className="option-list">
          {levelOptions.map(v => (
            <button key={v} className={`chip ${level === v ? 'active' : ''}`} data-value={v} onClick={()=>onSetLevel(v)}>{v === 'Collector' ? "Collector's Edition" : v}</button>
          ))}
        </div>
      </div>

      <div style={{marginTop:12}}>
        <h4>Product Status</h4>
        <div className="option-list">
          {statusOptions.map(v => (
            <button key={v} className={`chip ${status === v ? 'active' : ''}`} onClick={()=>onSetStatus(v)} data-value={v}>{v}</button>
          ))}
        </div>
      </div>

      <div className="range-wrap" style={{marginTop:12}}>
        <h4>Price (USD)</h4>
        <div className="range-controls" style={{marginTop:8}}>
          <input type="number" min="0" value={minPrice} onChange={(e)=>onSetMinPrice(Number(e.target.value || 0))} aria-label="Minimum price" />
          <div style={{color:'var(--muted)'}}>—</div>
          <input type="number" min="0" value={maxPrice} onChange={(e)=>onSetMaxPrice(Number(e.target.value || 0))} aria-label="Maximum price" />
        </div>
        <div className="double-range" style={{marginTop:10}}>
          <div className="range-track" aria-hidden="true">
            <div className="fill" style={{left: `${Math.max(0, (minPrice / 2000) * 100)}%`, width: `${Math.max(0, ((maxPrice - minPrice) / 2000) * 100)}%`}} />
          </div>
        </div>
        <div style={{marginTop:8}} className="muted">Tip: drag numbers or use inputs above</div>
      </div>

      <div style={{marginTop:12}}>
        <h4>Sort By</h4>
        <select id="sortSelect" className="sort" aria-label="Sort products" value={sort} onChange={e=>onSetSort(e.target.value)}>
          <option value="new">Newest arrivals</option>
          <option value="release_asc">Release date ↑</option>
          <option value="release_desc">Release date ↓</option>
          <option value="sales_desc">Best selling</option>
          <option value="fav_desc">Most saved</option>
          <option value="price_asc">Price: low → high</option>
          <option value="price_desc">Price: high → low</option>
        </select>
      </div>

      <button className="apply-btn" id="applyFilters" onClick={onApply}>Apply filters</button>
    </aside>
  );
}