// components/ProductCard.js
import React from 'react';

export default function ProductCard({ product, isFav, onToggleFav, onView }) {
  return (
    <article className="card">
      <div className="media" aria-hidden="false">
        <div className="badge">{product.edition}</div>
        <button className={`fav ${isFav ? 'active' : ''}`} data-id={product.id} aria-label="Save to favorites" onClick={()=>onToggleFav(product.id)}>
          <svg width="14" height="14" viewBox="0 0 24 24" fill="none"><path d="M20.8 7.6c0 6.2-8.8 12-8.8 12S3.2 13.8 3.2 7.6A4.4 4.4 0 018 3.2c1.8 0 3.4 1.1 4 2.6.6-1.5 2.2-2.6 4-2.6a4.4 4.4 0 014.8 4.4z" stroke="currentColor" strokeWidth="1.2" strokeLinecap="round" strokeLinejoin="round"/></svg>
        </button>
        <img loading="lazy" alt={product.title} src={product.img} onError={(e)=>{ e.currentTarget.src = '/images/fallback.svg'; }} />
      </div>

      <div className="card-body">
        <div style={{flex:1}}>
          <div className="title">{product.title}</div>
          <div className="subtitle">{product.sku} · {product.artist} · {product.availability}</div>
        </div>
        <div style={{textAlign:'right'}}>
          <div className="price">${product.price}</div>
          <div className="subtitle muted" style={{fontSize:12}}>Sold {product.sales}</div>
        </div>
      </div>

      <div className="card-actions">
        <button className="btn" data-id={product.id} data-action="view" onClick={()=>onView(product.id)}>View</button>
        <button className="btn" data-id={product.id} data-action="fav" onClick={()=>onToggleFav(product.id)}>{isFav ? 'Saved' : 'Save'}</button>
      </div>
    </article>
  );
}