// components/ProductGrid.js
import React from 'react';
import ProductCard from './ProductCard';

export default function ProductGrid({ items, favorites, toggleFav, onView }) {
  if (!items || items.length === 0) {
    return <div className="muted">No results. Try adjusting filters or search.</div>;
  }
  return (
    <div className="grid" aria-live="polite">
      {items.map(p => (
        <ProductCard key={p.id} product={p} isFav={!!favorites[p.id]} onToggleFav={toggleFav} onView={onView} />
      ))}
    </div>
  );
}