// components/Pagination.js
import React from 'react';

export default function Pagination({ totalItems, page, perPage, onPage }) {
  const totalPages = Math.max(1, Math.ceil(totalItems / perPage));

  const buttons = [];
  const maxButtons = 7;
  let start = Math.max(1, page - 3);
  let end = Math.min(totalPages, start + maxButtons - 1);
  if (end - start < maxButtons - 1) start = Math.max(1, end - maxButtons + 1);

  if (start > 1) {
    buttons.push({ label: '1', p: 1 });
    if (start > 2) buttons.push({ label: '…', p: null });
  }

  for (let i = start; i <= end; i++) buttons.push({ label: String(i), p: i });

  if (end < totalPages) {
    if (end < totalPages - 1) buttons.push({ label: '…', p: null });
    buttons.push({ label: String(totalPages), p: totalPages });
  }

  return (
    <div className="pagination" aria-label="Pagination">
      <button className="page-btn" onClick={()=> onPage(Math.max(1, page-1))}>Prev</button>
      {buttons.map((b, idx) => (
        b.p ? (
          <button key={idx} className={'page-btn' + (b.p === page ? ' active' : '')} onClick={()=>onPage(b.p)}>{b.label}</button>
        ) : (
          <div key={idx} className="muted" style={{padding:'8px'}}>…</div>
        )
      ))}
      <button className="page-btn" onClick={()=> onPage(Math.min(totalPages, page+1))}>Next</button>
    </div>
  );
}