// components/Modal.js
import React from 'react';

export default function Modal({ open, product, isFav, toggleFav, onClose }) {
  if (!open || !product) return null;
  return (
    <div className="modal-backdrop" style={{display:'flex'}} aria-hidden={!open}>
      <div className="modal" role="dialog" aria-modal="true" aria-labelledby="modalTitle">
        <button className="close-btn" onClick={onClose} aria-label="Close details">✕</button>

        <div style={{position:'relative'}}>
          <div className="media" id="modalImageWrap">
            <img id="modalImage" src={product.img} alt={product.title} style={{width:'100%', height:'100%', objectFit:'cover', display:'block'}}/>
          </div>
        </div>

        <div className="meta">
          <h2 id="modalTitle" style={{margin:0, marginBottom:8}}>{product.title}</h2>
          <div className="muted" id="modalSku" style={{marginBottom:12}}>{product.sku} · {product.artist}</div>
          <div style={{display:'flex',gap:12,alignItems:'center',marginBottom:12}}>
            <div style={{fontSize:20,fontWeight:800,color:'var(--accent1)'}} id="modalPrice">${product.price}</div>
            <div className="badge" id="modalEdition" style={{position:'static',background:'transparent',color:'var(--muted)',padding:'6px 8px',borderRadius:8,border:'1px solid rgba(255,255,255,0.03)'}}>{product.edition}</div>
          </div>

          <p className="muted" style={{marginTop:8}}>
            Limited/resin statue. Hand-painted finish. Edition: {product.edition}. Release date: {new Date(product.release).toLocaleDateString()}. This product has sold {product.sales} units.
          </p>

          <div style={{display:'flex',gap:10,marginTop:18}}>
            <button className="btn primary" id="modalFavBtn" onClick={()=>toggleFav(product.id)}>{isFav ? 'Saved' : 'Save'}</button>
            <button className="btn" id="modalDetailsPage" onClick={()=>{ /* route to product page later */ alert('Open product page'); }}>Open product page</button>
          </div>
        </div>
      </div>
    </div>
  );
}